.menu_container {
    padding: 20px 20px 0px 20px;
    color: white;
    font-family: 'Ownglyph_eunbyul21-Rg';
    letter-spacing: 4px;
}

.menu_container a {
    padding: 10px 20px 10px 20px;
    color: white;
    font-size: 20px;
    font-weight: bold;
    font-family: 'Ownglyph_eunbyul21-Rg';
    height: 8%;
    letter-spacing: 4px;
}

.menuBox {
    border-radius: 20px;
    display: flex;
    justify-content: space-around;
    background-color: black;
    z-index: 100;
    padding: 10px 10px;
    height: 100%;
}


.menuBox>a {
    height: 100%;
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    cursor: pointer;
}

.menuBox>a:hover {
    background-color: white;
    border-radius: 20px;
    color: black;
    transition: all 0.7s;
}