#aboutme::-webkit-scrollbar {
    background-color: black;
}

#aboutme::-webkit-scrollbar-thumb {
    background-color: grey;
    border: 5px solid white;
    border-radius: 20px;
}

#aboutme::-webkit-scrollbar-track {
    background-color: black;
}

#aboutme {
    position: relative;
    overflow-y: auto;
    display: flex;
    flex-direction: column;
    /* gap: 200px; */
}

#aboutme>h1 {
    text-align: center;
    background-color: forestgreen;
    background-color: rgba(255, 166, 0, 0.807);
    padding: 10px 0px;
}


.about_contentBox {
    display: flex;
    height: 100%;
}

.about_contentBox>div {
    width: 50%;
    overflow: hidden;
}

.about_contentBox>div:nth-child(1) {
    margin-right: 100px;
}

.about_contentBox .all_img {
    width: 100%;
    object-fit: contain;
}

#aboutme .modal {
    padding: 70px;
    position: relative;
    position: fixed;
    width: 50% !important;
    height: 75% !important;
    background-color: black;
    box-shadow: 0px 0px 10px 2px;
    border-radius: 20px;
    left: 25%;
    top: 14%;
    z-index: 10;
    overflow: hidden;
}

#aboutme .modal a {
    display: block;
    width: 50%;
    text-align: center;
    padding: 20px 10px;
    margin: auto;
    background-color: grey;
    color: black;
    font-size: 20px;
    font-weight: bold;
    margin-top: 30px;
}

#aboutme .modal a:hover {
    box-shadow: 0px 0px 20px 5px grey;
}

#aboutme .modal img {
    object-fit: contain;
    width: 100%;
}

#aboutme .modalBG {
    background-color: rgba(128, 128, 128, 0.656);
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    z-index: 9;
}

#aboutme .modal .xBtn {
    position: absolute;
    font-size: 30px;
    width: 50px;
    height: 50px;
    top: 10px;
    right: 10px;
    display: flex;
    justify-content: center;
    align-items: center;
    font-weight: bold;
    cursor: pointer;
}

#aboutme .modal .xBtn:hover {
    border-radius: 50px;
    background-color: white;
    color: black;
}

.str {
    font-weight: bold;
    font-size: xx-large;
}

/* ============================== */


.imgBox {
    width: 100%;
    overflow: hidden;
    height: 71%;
    display: flex;
}

.imgBox {
    width: 300%;
    display: flex;
    animation: slide 10s infinite;
}

@keyframes slide {
    0% {
        transform: translateX(0);
    }

    20% {
        transform: translateX(-33.3%);
    }

    40% {
        transform: translateX(-66.6%);
    }

    80% {
        transform: translateX(-33.3%);
    }

    100% {
        transform: translateX(0%);
    }
}

.about_aboutme {
    font-size: 25px;
    font-weight: bold;
}

