#portfolio_memory .about_contentBox>div:nth-child(1) {
    display: flex;
    justify-content: center;
    align-items: center;
}

#portfolio_memory .about_contentBox {
    margin-bottom: 100px;
}

#portfolio_memory .about_contentBox .skill_title {
    color: cadetblue;
}

#portfolio_memory p {
    font-size: 18px;
}

#portfolio_memory .about_contentBox img {
    width: 100%;
}


#portfolio_memory>hr {
    margin-top: 100px;
}

#portfolio_memory>h1 {
    margin-bottom: 70px;
    text-align: center;
    background-color: rgba(0, 255, 255, 0.881);
    padding: 10px 10px;
    color: black;
}

#portfolio_memory .card-back {
    position: initial;
    backface-visibility: initial;
    padding: 0;
    transform: rotate(0);
}