
.project_container {
    height: 100%;
    display: flex;
    flex-wrap: wrap;
    gap: 50px;
}


.project_container>a {
    width: calc(33.3333% - 33.3999px);
    padding: 30px;
    border: 1px solid rgba(255, 255, 255, 0.453);
    border-radius: 20px;
    word-break: keep-all;
    overflow: hidden;
    min-width: 510px;
}

.project_container>a:hover {
    box-shadow: 0px 0px 20px 5px;
}

.project_container p {
    font-size: 30px;
    text-align: center;
    margin: 20px 30px 50px 30px;
}

.project_container img {
    object-fit: contain;
    width: 100%;
}


#portfolio_memory img:last-child {
    margin-top: 40px;
}
