* {
    padding: 0;
    margin: 0;
    box-sizing: border-box;
}

@font-face {
    font-family: 'Ownglyph_eunbyul21-Rg';
    src: url('https://fastly.jsdelivr.net/gh/projectnoonnu/2405-2@1.0/Ownglyph_eunbyul21-Rg.woff2') format('woff2');
    font-weight: normal;
    font-style: normal;
}

a {
    text-decoration: none;
    color: white;
}

@font-face {
    font-family: 'Ownglyph_eunbyul21-Rg';
    src: url('https://fastly.jsdelivr.net/gh/projectnoonnu/2405-2@1.0/Ownglyph_eunbyul21-Rg.woff2') format('woff2');
    font-weight: normal;
    font-style: normal;
}

.App {
    height: 100vh;
    overflow: hidden;
    background-color: rgb(128, 128, 128);
    font-family: 'Ownglyph_eunbyul21-Rg';
    letter-spacing: 2px;
}

.fixedBtn {
    text-align: center;
    position: fixed;
    right: 3%;
    top: 14.5%;
    display: flex;
    flex-direction: column;
    gap: 6px;
}

.fixedBtn>div {
    border-radius: 10px;
    padding: 10px 10px;
    cursor: pointer;
    background-color: rgba(137, 43, 226, 0.62);
    letter-spacing: 5px;
    z-index: 8;
}

.tlinkBox {
    width: 100%;
    display: flex;
    justify-content: space-around;
    text-align: center;
    margin-bottom: 80px;
}

.tlinkBox>a {
    border-radius: 10px;
    padding: 10px;
    width: 43%;
    background-color: grey;
    color: black;
    font-weight: bold;
    font-size: 30px;
}

.tlinkBox>a:hover {
    background-color: aqua;
    box-shadow: 0px 0px 10px 3px aqua;
}



#main_container {
    width: 100%;
    height: 92%;
    padding: 20px 20px 10px 20px;
    overflow-y: auto;
}

#main_container>div {
    background-color: rgb(0, 0, 0);
    border-radius: 20px;
    width: 100%;
    height: 100%;
    color: white;
    padding: 60px;
    overflow-y: auto;
}

#main_container>div::-webkit-scrollbar {
    background-color: black;
}

#main_container>div::-webkit-scrollbar-thumb {
    background-color: grey;
    border: 5px solid white;
    border-radius: 20px;
}

#main_container>div::-webkit-scrollbar-track {
    background-color: black;
}