

#main_container {
    width: 100%;
    height: 92%;
    padding: 20px 20px 10px 20px;
    overflow-y: auto;
}

#main_container>div {
    background-color: rgb(0, 0, 0);
    border-radius: 20px;
    width: 100%;
    height: 100%;
    color: white;
    padding: 60px;
    overflow-y: auto;
}

#home {
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 100px 150px;
    gap: 80px;
    color: white;
    text-wrap: nowrap;
}

#home>div {
    height: 100%;
    border: 1px solid rgba(128, 128, 128, 0.692);
    border-radius: 20px;
    overflow: hidden;
    position: relative;
}

#home>div:hover {
    box-shadow: 0px 0px 20px 1px;
    border-radius: 20px;
}

#idCard:hover {
    cursor: pointer;
}

#idCard {
    width: 60%;
    min-width: 800px;
    perspective: 1000px;
}


.card {
    width: 100%;
    height: 100%;
    position: relative;
    transition: transform 0.6s;
    transform-style: preserve-3d;
}

.card-front,
.card-back {
    width: 100%;
    height: 100%;
    position: absolute;
    backface-visibility: hidden;
}

.card-front {
    display: grid;
    grid-template: 2fr 2fr 1fr / repeat(5, 1fr);
    overflow: hidden;
}

.card-back {
    color: white;
    transform: rotateY(-180deg);
    padding: 60px;
}

.flipped {
    transform: rotateY(-180deg);
}

#idCard>div {
    text-align: center;
}

.card-back>div {
    display: flex;
    gap: 10px;
}

.card-back>div:not(:nth-child(1)) {
    margin-top: 60px;
}

.skillBox {
    width: 100px;
    height: 100px;
}

.skillBox>img {
    border-radius: 30px;
    width: 100%;
    height: 100%;
    object-fit: cover;
    display: block;
}

#privacy {
    grid-column: span 3;
    grid-row: span 2;
    padding: 60px 60px 0px 60px;
    text-wrap: nowrap;
    width: 100%;
}

#privacy>div {
    height: 25%;
}

#privacy>div:nth-child(1) {
    font-size: 60px;
    letter-spacing: 30px;
    text-indent: 27px;
}

#privacy>div:nth-child(2) {
    letter-spacing: 7px;
}

#privacy>div:nth-child(2)>p:nth-child(1) {
    font-size: 30px;
}

#privacy>div:nth-child(3) {
    letter-spacing: 5px;
    display: flex;
    flex-direction: column;
    gap: 3px;
}

#imgBox {
    grid-column: span 2;
    grid-row: span 2;
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 60px 40px 100px 35px;
}

#imgBox img {
    object-fit: cover;
    width: 100%;
    height: 100%;
}

#contact_me {
    grid-column: span 5;
    justify-content: left;
    padding: 0px 50px;
    display: flex;
    justify-content: space-around;
}

#contact_me>div,#contact_me>a {
    display: flex;
    justify-content: center;
    align-items: center;
    letter-spacing: 2px;
}

#contact_me span {
    margin-right: 10px;
    font-size: 25px;
}

#title_forme {
    width: 40%;
    padding: 60px;
}

#title_forme h2:not(:nth-child(1)) {
    margin-top: 40px;
}

#title_forme p {
    letter-spacing: 2px;
    text-indent: 38px;
    margin-top: 7px;
}

#title_forme span {
    color: grey;
    font-weight: bold;
    margin-left: 15px;
    font-size: larger;
}
/* ============================ */

