#troubles {
    display: flex;
    flex-direction: column;
}


.ttomatto {
    width: 100%;
    display: flex;
    justify-content: space-around;
    text-align: center;
    margin-bottom: 80px;
}

.ttomatto>a {
    border-radius: 10px;
    padding: 10px;
    width: 43%;
    background-color: grey;
    color: black;
    font-weight: bold;
    font-size: 30px;
}

.ttomatto>a:hover {
    background-color: #9b1b20;
    box-shadow: 0px 0px 10px 3px #9b1b20;
}